* {
    box-sizing: border-box;
}

::selection {
    background-color: $clr1;
    color: #fff;
}

html {
    font-family: montserrat, sans-serif;
    color: $clr2;
}

body {
    margin: 0;
}

img {
    width: 100%;
}

h1,h2,h3,h4,h5,h6 {
    font-weight: 400;
    font-family: montserrat, serif;
    text-transform: capitalize;
}



li {
    list-style: none;
}

a {
    color: inherit;
    text-decoration: none;
}

button {
    cursor: pointer;
}